@import 'normalize';
@import 'visuallyhidden';
@import 'fonts';
@import 'variables';
@import 'mixins';

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style-type: none;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

img {
  max-width: 100%;
  display: block;
}

body {
  font-family: var(--custom-font);
  color: var(--text-main);
  font-size: 18px;
  font-weight: 400;
}

.container {
  width: 100%;
  max-width: 1670px;
  margin: 0 auto;
  padding: 0 10px;
}

.btn {
  font-family: var(--custom-font);
  border: none;
  line-height: 1;
  text-align: center;
  background: transparent;
  cursor: pointer;

  &:focus {
    outline: none;
  }
}

.link-response {
  transition: opacity .3s ease-in-out;

  &:hover {
    opacity: .7;
  }

  &:active {
    opacity: .5;
  }

  &:focus {
    opacity: .5;
    outline: none;
  }
}

.centered {
  object-fit: contain;
  object-position: center center;
}

.eco {
  padding-top: 80px;
  padding-bottom: 40px;

  &__grid {
    width: 100%;
    max-width: 1590px;
    margin-left: auto;
    display: grid;
    grid-template-areas: "top top top"
                          "small description large"
                          "bottom bottom large";
    grid-template-columns: 22% 33.2% 1fr;
    row-gap: 40px;
    overflow: hidden;
  }

  &__top {
    grid-area: top;
    height: 20px;
    position: relative;
    z-index: 0;
  }

  &__top-image {
    width: 100vw;
    height: 235px;
    max-width: 1330px;
    position: absolute;
    top: 0;
    right: 0;
    z-index: 0;
    object-fit: unset;
  }

  &__small {
    grid-area: small;
    z-index: 2;
    align-self: flex-end;
    justify-self: end;
  }

  &__small-image {
    width: 100%;
    max-width: 300px;
    max-height: 340px;
  }

  &__description {
    padding-left: 45px;
    grid-area: description;
    z-index: 2;
  }

  &__heading {
    font-size: 33px;
    color: var(--text-secondary);
    font-weight: 400;
    margin-bottom: 36px;
  }

  &__heading-bottom {
    display: block;
    font-size: 42px;
    color: var(--text-main);
    font-weight: 600;
  }

  &__text {
    margin-bottom: 25px;
    line-height: 1.45;
  }

  &__link {
    color: var(--text-main);
  }

  &__large {
    padding-left: 45px;
    padding-right: 20px;
    padding-bottom: 50px;
    align-self: flex-end;
    grid-area: large;
    z-index: 2;
  }

  &__large-image {
    width: 100%;
    max-width: 610px;
    max-height: 775px;
  }

  &__bottom {
    justify-self: flex-end;
    grid-area: bottom;
    z-index: 2;
  }

  &__bottom-image {
    width: 100%;
    max-width: 870px;
    max-height: 385px;

  }
}

.cards {
  padding-top: 50px;
  padding-bottom: 40px;

  &__container {
    max-width: 1470px;
  }

  &__grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    column-gap: 45px;
    row-gap: 45px;
  }

  &__card {
    position: relative;
    overflow: hidden;
  }

  &__overlay {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
  }

  &__heading {
    margin-bottom: 20px;
    font-size: 30px;
    color: var(--text-white);
  }

  &__link {
    font-size: 20px;
    color: var(--text-white); 
  }

  &__image {
    margin: 0 auto;
    width: 100%;
    max-width: 330px;
    height: 660px;
    object-fit: cover;
  }
}

.services {
  padding-top: 50px;

  &__grid {
    display: grid;
    grid-template-areas: "info news bg";
    grid-template-columns: 1.15fr 1fr 60px;
    overflow: hidden;
  }

  &__bg {
    grid-area: bg;
    position: relative;
    z-index: 0;
  }

  &__bg-image {
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    height: 390px;
    width: 100vw;
    max-width: 1500px;
    z-index: 0;
  }

  &__info {
    grid-area: info;
    padding-left: 60px;
    align-self: center;
    justify-self: flex-end;
    z-index: 2;
  }

  &__heading {
    font-size: 40px;
    margin-bottom: 40px;
    max-width: 650px;
  }

  &__bottom-text {
    display: flex;
    flex-direction: row;
  }

  &__left-text {
    width: 28%;
  }

  &__right-text {
    width: 72%;
  }

  &__time {
    margin-bottom: 20px;
    font-size: 20px;
    color: var(--text-secondary);
    display: block;
    letter-spacing: 0.02em;
  }

  &__link {
    font-weight: 600;
    color: var(--text-main);
    letter-spacing: 0.05em;
  }

  &__text {
    font-size: 20px;
    line-height: 1.4;
  }

  &__news {
    grid-area: news;
    justify-self: flex-end;
    z-index: 2;
  }

  &__news-image {
    max-height: 495px;
  }
}

@include query(1400px) {

  .services {
    &__bottom-text {
      flex-direction: column;
    }
  
    &__left-text {
      width: 100%;
      margin-bottom: 20px;
    }
  
    &__right-text {
      width: 100%;
    }
  }
}

@include query(1200px) {

  .eco {
    &__grid {
      grid-template-areas: "top top"
                            "small description"
                            "large large"
                            "bottom bottom";
      grid-template-columns: 40% 60%;
    }

    &__text {
      margin-right: 20px;
    }

    &__large {
      padding: 0;
      justify-self: center;
    }

    &__bottom {
      justify-self: center;
    }
  }

  .services {
      
    &__grid {
      display: grid;
      grid-template-areas: "bg"
                            "info"
                            "news";
      grid-template-columns: 1fr;
      row-gap: 40px;
    }
  
    &__bg-image {
      top: 0;
      transform: unset;
    }

    &__info {
      justify-self: flex-start;
    }

    &__text {
      max-width: 700px;
    }

    &__news{
      justify-self: center;
    }
  }
}

@include tablet-portrait {

  .eco {
    &__grid {
      grid-template-areas: "top"
                            "description"
                            "small"
                            "large"
                            "bottom";
      grid-template-columns: 100%;
    }

    &__small {
      align-self: center;
      justify-self: center;
    }
  }

  .services {

    &__heading {
      font-size: 32px;
      max-width: 500px;
    }

  }
}

@include phone-large {

  .eco {
    padding-top: 20px;
    padding-bottom: 20px;

    &__grid {
      row-gap: 20px;
    }

    &__description {
      padding-left: 0;
    }
  }

  .cards {
    padding-top: 20px;
    padding-bottom: 20px;

    &__grid {
      row-gap: 20px;
      grid-template-columns: 1fr;
    }
  }

  .services {
    padding-top: 20px;

    &__info {
      padding-left: 10px;
    }
  }
}